<template>
  <div id="upload-app">
    <h2>Upload files</h2>
    <div class="content">
      <form action="/music/database/upload" enctype="multipart/form-data" method="post">
        <div id="inputs-container">
          <input
            type="file"
            :name="i"
            accept="audio/mp3"
            v-for="i in range"
            :key="i"
            @change="FileChange($event)"
          />
        </div>
        <div class="controls-panel">
          <button id="add-button" @click.prevent="AddUpload()">+</button>
          <button id="add-button" @click.prevent="DelUpload()">-</button>
          <input class="form-submit-button" type="submit" value="Upload" />
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      range: 1
    };
  },
  methods: {
    AddUpload() {
      this.range++;
    },
    DelUpload() {
      this.range--;
    },
    FileChange(e) {
      if (e.target.value) this.range++;
      console.log(e.target.value);
    }
  }
};
</script>


<style lang="scss" scoped>
#inputs-container {
  display: block;
  flex-direction: row;

  input {
    display: flex;
    width: 100%;
    padding: 11px;
    border-bottom: 1px solid #cccccc;
  }
}
.controls-panel {
  margin-top: 20px;
}
</style>


