<template>
  <div class="downloaded-app">
    <h2>Downloaded files</h2>
    <div class="content">
      <ul>
        <div class="sort-buttons">
          <div></div>
          <div class="button" @click="SortByTitle()">
            Title:
            <span v-if="sortedBy === 'title'">&#8659;</span>
          </div>
          <div class="button" @click="SortByArtist()">
            Artist:
            <span v-if="sortedBy === 'artist'">&#8659;</span>
          </div>
          <div class="button" @click="SortByAlbum()">
            Album:
            <span v-if="sortedBy === 'album'">&#8659;</span>
          </div>
          <div class="checkall-button">
            <input type="checkbox" :checked="AllChecked" @change="CheckAll()" />
          </div>
        </div>
        <div class="tracks-list" v-for="(track, index) in tracks" :key="index">
          <li class="track">
            <div class="track-index">{{index + 1}}.</div>
            <div class="track-title">
              <div class="show-title">{{track.title}}</div>
              <div class="show-filename">
                <span>File name:</span>
                {{track.fileName}}
              </div>
            </div>
            <div class="track-artist">{{track.artist}}</div>
            <div class="track-album">{{track.album}}</div>
            <input class="track-check" type="checkbox" v-model="track.checked" name="del-check" />
            <div class="line">&#160;</div>
          </li>
        </div>
      </ul>
      <div class="controls-panel">
        <div class="CountChecked">{{GetChecked.length}} tracks</div>
        <button @click="DeleteChecked()" id="delete-button">Delete</button>
        <button @click="GetSongs()">Refresh</button>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      tracks: [],
      sortedBy: "",
      allChecked: false
    };
  },
  computed: {
    GetChecked() {
      return this.tracks
        .filter(track => {
          return track.checked;
        })
        .map(track => track.fileName);
    },
    AllChecked() {
      return this.GetChecked.length == this.tracks.length;
    }
  },
  methods: {
    SortByArtist() {
      this.tracks = this.tracks.sort((a, b) => {
        if (a.artist > b.artist) return 1;
        if (a.artist < b.artist) return -1;
      });
      this.sortedBy = "artist";
    },
    SortByTitle() {
      this.tracks = this.tracks.sort((a, b) => {
        if (a.title > b.title) return 1;
        if (a.title < b.title) return -1;
      });
      this.sortedBy = "title";
    },
    SortByAlbum() {
      this.tracks = this.tracks.sort((a, b) => {
        if (a.album > b.album) return 1;
        if (a.album < b.album) return -1;
      });
      this.sortedBy = "album";
    },
    CheckAll() {
      this.tracks.forEach(track => {
        track.checked = event.target.checked;
      });
    },
    GetSongs() {
      this.$http.get("/music/database/music").then(res => {
        this.tracks = res.body;
        this.SortByAlbum();
        this.SortByArtist();
        this.SortByTitle();
      });
    },
    DeleteChecked() {
      if (this.GetChecked[0]) {
        this.$http
          .post(
            "/music/database/delete",
            { titles: this.GetChecked },
            { emulateJSON: true }
          )
          .then(res => {
            this.GetSongs();
          });
      }
    }
  },
  created() {
    this.GetSongs();
  }
};
</script>

<style lang="scss" scoped>
ul {
  padding-inline-start: 0;
}

.track,
.sort-buttons {
  display: grid;
  grid-template-columns: 25px 250px 150px 250px 25px; //repeat(3, 25vw)
  grid-template-rows: 35px 1px;
  grid-column-gap: 14px;
  grid-row-gap: 8px;
  align-items: center;
  justify-content: center;

  > div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .show-filename {
    display: none;
  }

  .line {
    grid-column: 1 / 6;
    border-top: 1px solid #cccccc;
  }
}

.track:hover {
  & > div:not(.track-index):not(.line) {
    visibility: hidden;
    overflow: visible;
  }

  .show-filename {
    visibility: visible;
    display: flex;
    line-height: 1.5;

    span {
      padding: 0 6px;
      background-color: #dddddd;
      margin-right: 5px;
    }
  }
  .show-title {
    display: none;
  }
}

.sort-buttons {
  .button {
    padding: 16px;

    &:hover {
      text-decoration: underline;
    }
  }
}
</style>


